.date__style {
  flex-wrap: wrap;
  margin-bottom: 30px;
  margin-left: 20px;
  display: flex;
}

.date__day, .date, .date__month, .date__year {
  margin-left: 30px;
}

.clock {
  width: 500px;
  height: 500px;
  background-image: url("clock-face-time-digital-clock-number-clock-a274e8426c7fa20fa6da28b8e4b24d4e.89c1a3a9.png");
  background-color: fafafa;
  background-size: cover;
  border-radius: 50%;
  position: relative;
  left: 500px;
}

.clock__arrow {
  transform-origin: bottom;
  background-color: #000;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  position: absolute;
  transform: translateX(-50%);
}

.clock__seconds__arrow {
  width: 5px;
  height: 45%;
  bottom: 50%;
  left: 50%;
}

.clock__minutes__arrow {
  width: 10px;
  height: 35%;
  bottom: 50%;
  left: 50%;
}

.clock__hours__arrow {
  width: 20px;
  height: 25%;
  bottom: 50%;
  left: 50%;
}

.clock__center {
  width: 26px;
  height: 26px;
  background-color: #000;
  border-radius: 50%;
  position: absolute;
  top: 237px;
  left: 240px;
}

